.toggle_mobile_menu {
    display: none;
    width: 30px;
    height: 30px;
    align-items: center;
    justify-content: center;
    background: transparent;
    flex-direction: column;
    margin-left: 15px;
    border: none;
    outline: none !important;
    transition: all .3s;
    &.active {
        span {
            &:nth-of-type(2) {
                transform: translateX(-40px);
                opacity: 0;
            }
            &:nth-of-type(1) {
                transform: rotate(-35deg);
                transform-origin: right;
            }
            &:nth-of-type(3) {
                transform: rotate(35deg);
                transform-origin: right;
            }
        }
    }
    span {
        width: 24px;
        height: 2px;
        background: #f3f3f3;
        margin-bottom: 5px;
        display: block;
        transition: all .3s;
        transform-origin: right;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}

.mobile_menu {
    position: fixed;
    top: 0;
    left: -100%;
    height: 100vh;
    width: 80vw;
    background-color: #e00000bf;
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 1s;
    &.active {
        left: 0;
    }
    ul {
        padding-left: 0;
        margin-bottom: 0;
    }
    li {
        list-style: none;
        text-align: center;
        margin-bottom: 15px;
        font-family: Vollkorn;
        font-size: 15px;
        font-weight: 400;
        line-height: 27px;
        transition: all .3s;
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 5px;
        a {
            color: #ffffff;
        }
    }
}