/**
  Десктоп маленький
 */

@media screen and (max-width: 1199px) {
    .menu .menu_item .text {
        margin-right: 0;
    }
}


/**
  Планшет горизонтальный
 */

@media screen and (max-width: 991px) {
    .main .main_container {
        padding-bottom: 110px;
    }
    .contact .contact_container {
        margin-top: -235px;
    }
    .dishes .dishes_item {
        margin-bottom: 15px;
    }
    .menu .menu_item .text {
        width: 100%;
        p {
            height: 50px;
            overflow: hidden;
            width: 200px;
        }
    }
    .menu p {
        margin-bottom: 55px;
    }
    footer button {
        padding-top: 14px;
        padding-bottom: 14px;
        padding-left: 30px;
        padding-right: 30px;
    }
    footer p {
        font-size: 12px;
    }
}


/**
  Планшет вертикальный
 */

@media screen and (max-width: 767px) {
    .main .main_container img {
        padding-left: 179px;
    }
    .main .main_container {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .main .main_container h1 {
        font-size: 45px;
        line-height: 45px;
    }
    .main .main_container h3 {
        margin-bottom: 25px;
    }
    .main .main_container img {
        margin-bottom: 35px;
    }
    .contact .contact_container {
        margin-top: 0;
        max-width: 100%;
    }
    .contact .contact_overlay {
        max-width: 100%;
        height: 300px;
    }
    .dishes p {
        margin-bottom: 50px;
    }
    .dishes {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .menu .menu_item .text p {
        width: 184px;
    }
    .menu .menu_item {
        margin-right: 0;
    }
    .menu .menu_item .text h4 {
        line-height: 24.8px;
    }
    .menu .menu_item .price span {
        font-size: 30px;
    }
    .choose form label {
        width: 100%;
    }
    .choose form {
        margin-top: 50px;
    }
    .choose form label p {
        margin-top: 15px;
    }
    .choose form button {
        margin-top: 40px;
    }
    footer .view_map {
        margin-bottom: 30px;
    }
    footer .footer_line {
        max-width: 100%;
    }
    footer .social_description {
        max-width: 100%;
    }
}


/**
  Мобильные устройства
 */

@media screen and (max-width: 574px) {
    .header>ul {
        display: none;
    }
    .toggle_mobile_menu {
        display: flex;
    }
    .main .main_container img {
        padding-left: 0;
        display: block;
        margin: auto;
        margin-bottom: 35px;
    }
    .contact {
        padding-top: 60px;
        padding-bottom: 30px;
    }
    .dishes {
        padding-bottom: 40px;
    }
    .menu .menu_item .text p {
        max-width: 240px;
        width: 100%;
    }
    .menu .menu_container h3::before {
        width: 50px;
    }
    .menu .menu_container h3::after {
        width: 50px;
    }
    .menu h2 {
        margin-bottom: 25px;
    }
    .menu p {
        margin-bottom: 35px;
    }
    .menu h3 {
        margin-bottom: 50px;
    }
    .menu .menu_item .price {
        padding-top: 40px;
    }
    .menu {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .choose form input {
        font-size: 15px;
    }
    .choose form {
        margin-top: 20px;
    }
    .choose form label select {
        font-size: 15px;
    }
    .choose {
        padding-top: 55px;
        padding-bottom: 55px;
    }
    footer .footer_social {
        max-width: 250px;
        margin-bottom: 30px;
    }
    footer .copyright {
        margin-top: 30px;
    }
    footer {
        padding-top: 40px;
        padding-bottom: 40px;
    }
}